@use "global" as *;

.breadcrumb li {
  display: inline;/*横に並ぶように*/
}

.breadcrumb li:after {
  content: '>';
  padding: 0 rem(6);
  color: $gray-sub;
}

.breadcrumb li:last-child:after {
  content: '';
}

.breadcrumb li a {
  color: $gray;
  display: inline-block;
  padding: rem(6) rem(2);
  transition: .3s;

  @include mq() {
    padding: rem(6) rem(4);
  }
}

.breadcrumb li:first-child a:before {
  font-family: FontAwesome;
  content: '\f015';
  color: $blue;
  padding: rem(4);
}

.breadcrumb li a:hover {

  @media (any-hover:hover) {
    border-bottom: 1px solid $blue;
  }
}