@charset "UTF-8";
@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;500;700&display=swap");
@font-face {
  font-family: "myfont";
  src: url("../ROLAchan/ROLAchan-Regular.otf") format("opentype");
}
body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4545454545vw;
  }
}
@media (min-width: 1100px) {
  html {
    font-size: 16px;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
}
@media (any-hover: hover) {
  a {
    transition: opacity 0.3s;
    cursor: pointer;
  }
}

body {
  color: #111;
  font-weight: 400;
  letter-spacing: 0.05em;
  background: #fff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-wrap: break-word;
  display: flex;
  flex-flow: column;
  min-height: 100vh;
}

main {
  flex: 1;
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 400;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: bottom;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default padding */
ul,
ol {
  padding: 0;
}

/* Remove default margin */
body,
h1,
h2,
h3,
h4,
p,
ul,
ol,
figure,
blockquote,
dl,
dd {
  margin: 0;
}

/* Set core root defaults */
html {
  scroll-behavior: smooth;
}

/* Set core body defaults */
body {
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* Remove list styles on ul, ol elements with a class attribute */
ul,
ol,
summary {
  list-style: none;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img {
  max-width: 100%;
  display: block;
  width: 100%;
}

/* Natural flow and rhythm in articles by default */
article > * + * {
  margin-top: 1em;
}

/* Inherit fonts for inputs and buttons */
input,
button,
textarea,
select {
  font: inherit;
}

/* Blur images when they have no alt attribute */
img:not([alt]) {
  filter: blur(10px);
}

/* フォームリセット */
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  font: inherit;
  outline: none;
  padding: 0;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

.about__title {
  padding-top: 3.75rem;
  text-align: center;
}

.about__inner {
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .about__inner {
    margin-top: 5rem;
  }
}

.about__sub-title {
  font-size: 1.25rem;
  font-weight: 500;
}
@media screen and (min-width: 768px) {
  .about__sub-title {
    font-size: 1.5rem;
  }
}

.about__items {
  margin-top: 2.5rem;
  background: #c8ddf4;
  border-radius: 33% 67% 66% 34%/31% 56% 44% 69%;
}
@media screen and (min-width: 768px) {
  .about__items {
    margin-top: 3.75rem;
    border-radius: 72% 28% 66% 34%/22% 54% 46% 78%;
  }
}

.about__item:not(:first-child) {
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .about__item:not(:first-child) {
    margin-top: 2rem;
  }
}

.about__category {
  font-size: 1.125rem;
  position: relative;
  display: inline-block;
  padding-right: 0.5rem;
}
@media screen and (min-width: 768px) {
  .about__category {
    font-size: 1.25rem;
  }
}

.about__category::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  height: 1.25rem;
  width: 0.0625rem;
  background: #0052D4;
}

.about__text {
  line-height: 1.6;
  text-align: justify;
}

.breadcrumb li {
  display: inline;
  /*横に並ぶように*/
}

.breadcrumb li:after {
  content: ">";
  padding: 0 0.375rem;
  color: #a9a9a9;
}

.breadcrumb li:last-child:after {
  content: "";
}

.breadcrumb li a {
  color: #333;
  display: inline-block;
  padding: 0.375rem 0.125rem;
  transition: 0.3s;
}
@media screen and (min-width: 768px) {
  .breadcrumb li a {
    padding: 0.375rem 0.25rem;
  }
}

.breadcrumb li:first-child a:before {
  font-family: FontAwesome;
  content: "\f015";
  color: #0052D4;
  padding: 0.25rem;
}

@media (any-hover: hover) {
  .breadcrumb li a:hover {
    border-bottom: 1px solid #0052D4;
  }
}

.btn-top {
  display: inline-block;
  border: 1px solid #333;
  background-color: #fff;
  text-align: center;
  transition: 0.3s ease;
  line-height: 1;
  color: #333;
  font-size: 1rem;
  padding: 0.4375rem 0.625rem;
}
@media screen and (min-width: 768px) {
  .btn-top {
    position: relative;
    font-size: 1.125rem;
    z-index: 10;
    width: min(100%, 15.625rem);
    padding: 1rem 0;
  }
}

@media (any-hover: hover) {
  .btn-top:hover {
    color: #fff;
    transition: 0.3s;
  }
  .btn-top:hover:hover {
    background-color: #333;
  }
}

@media screen and (min-width: 768px) {
  .btn-top::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    width: 0;
    display: block;
    background-color: #333;
    z-index: -1;
    transition: 0.3s;
  }
}

@media screen and (min-width: 768px) {
  .btn-top:hover::before {
    width: 100%;
  }
}

.btn {
  display: flex;
  align-items: center;
  position: relative;
  padding: 1.875rem 0 1.875rem 2.0625rem;
  color: #111;
  font-size: 1.125rem;
  font-weight: 500;
  margin: 0 auto;
}

.btn span {
  position: relative;
  padding: 0.625rem;
  background: #fff;
  z-index: 1;
  transition: 0.3s;
}

@media (any-hover: hover) {
  .btn:hover span {
    background: #c8ddf4;
  }
}

.btn::before {
  content: "";
  transform: rotate(45deg);
  margin-right: 0.625rem;
  width: 1rem;
  height: 1rem;
  border-top: 3px solid #111;
  border-right: 3px solid #111;
  z-index: 10;
}

.btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  width: 5rem;
  height: 5rem;
  border: 2px solid #0052D4;
  border-radius: 50%;
  transition: 0.3s;
}

@media (any-hover: hover) {
  .btn:hover::after {
    left: -50%;
    background: #c8ddf4;
    width: 15.625rem;
    border-radius: 280px;
  }
}

/* カード単体 */
.card {
  display: block;
  margin-right: auto;
  margin-left: auto;
  width: min(100%, 37.5rem);
  transition: 0.3s;
}

.card__img {
  aspect-ratio: 1500/822;
  width: 100%;
  transition: 0.3s;
  position: relative;
}

.card__img::before {
  content: "";
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 0%;
  height: 0%;
  background: rgba(255, 255, 255, 0.4);
  background: rgba(0, 82, 212, 0.2);
  transition: 0.3s;
  z-index: 20;
}

.card__img img {
  height: 100%;
  object-fit: cover;
  transition: 0.3s;
  overflow: hidden;
}

.card__info {
  padding: 1rem 0.5rem;
  line-height: 1.5;
}

.card__title {
  font-size: 1.125rem;
}
@media screen and (min-width: 768px) {
  .card__title {
    font-size: 1.25rem;
  }
}

.card__link,
.card__type {
  color: #a9a9a9;
}

@media (any-hover: hover) {
  .card:hover .card__img::before {
    width: 100%;
    height: 100%;
    transform: scale(1.1);
    border-radius: 10px;
  }
}

@media (any-hover: hover) {
  .card:hover .card__img img {
    transform: scale(1.1);
    border-radius: 10px;
  }
}

/* カード複数 */
@media screen and (min-width: 768px) {
  .cards {
    display: flex;
    flex-wrap: wrap;
    /* コンテンツが増えた時に下に折り返す */
  }
}

.cards__item:not(:first-child) {
  margin-top: 1.25rem;
}
@media screen and (min-width: 768px) {
  .cards__item:not(:first-child) {
    margin-top: 0;
  }
}

/* 枚数が増えた時用と2枚目以降余白に*/
@media screen and (min-width: 768px) {
  .cards__item:not(:nth-of-type(4n + 1)) {
    margin-left: 1.5rem;
  }
}

@media screen and (min-width: 768px) {
  .cards__item:nth-of-type(n + 5) {
    margin-top: 1.5rem;
  }
}

@media screen and (min-width: 768px) {
  .cards--col4 > .cards__item {
    width: min(100%, (100% - 4.5rem) / 4);
  }
}

.footer {
