  }
}

.footer__items {
  display: flex;
  justify-content: center;
}

.footer__item a {
  display: inline-block;
  width: 100%;
  font-size: 1.25rem;
  padding: 0.5rem 1rem;
  letter-spacing: 0.15em;
}
@media screen and (min-width: 768px) {
  .footer__item a {
