    font-size: 2rem;
  }
}

.footer__copyright {
  line-height: 2;
}
@media screen and (min-width: 768px) {
  .footer__copyright {
    padding-top: 0.5rem;
  }
}

.footer__copyright small {
  font-size: 0.75rem;
  letter-spacing: 0.15em;
}
@media screen and (min-width: 768px) {
  .footer__copyright small {
    font-size: 0.875rem;
  }
}

.hamburger {
  padding: 0.5rem;
  z-index: 70;
}
@media screen and (min-width: 768px) {
  .hamburger {
    padding: 1rem;
  }
}

.hamburger.is-change {
  background: rgba(255, 255, 255, 0.8);
  border-radius: 10px;
}

.hamburger span {
  position: relative;
  display: block;
  height: 0.125rem;
  background: #0052D4;
  transition: 0.5s;
  margin: 0 auto;
}

.hamburger-l {
  width: 3.125rem;
}
@media screen and (min-width: 768px) {
  .hamburger-l {
    width: 6.25rem;
  }
}

.hamburger-m {
  width: 2.1875rem;
}
@media screen and (min-width: 768px) {
  .hamburger-m {
    width: 4.6875rem;
  }
}

.hamburger-s {
  width: 1.25rem;
}
@media screen and (min-width: 768px) {
  .hamburger-s {
    width: 3.125rem;
  }
}

@media (any-hover: hover) {
  .hamburger:hover span {
    width: 6.25rem;
  }
}

.hamburger span:not(:first-child) {
  margin-top: 0.375rem;
}
@media screen and (min-width: 768px) {
  .hamburger span:not(:first-child) {
    margin-top: 0.75rem;
  }
}

/*is-OPEN時の動き*/
.hamburger.is-open span {
  background: #111;
}

.hamburger.is-open span:nth-child(1) {
  top: 0.375rem;
  transform: rotate(135deg);
}
@media screen and (min-width: 768px) {
  .hamburger.is-open span:nth-child(1) {
    top: 1.375rem;
  }
}

.hamburger.is-open span:nth-child(2) {
  top: 0.375rem;
  transform: rotate(135deg);
  width: 3.125rem;
}
@media screen and (min-width: 768px) {
  .hamburger.is-open span:nth-child(2) {
    width: 6.25rem;
    top: 1.375rem;
  }
}

.hamburger.is-open span:nth-child(3) {
  transform: translateY(-50%);
  opacity: 0;
}

.hamburger.is-open span:nth-child(4) {
  top: -1.125rem;
  transform: rotate(-135deg);
  width: 3.125rem;
}
@media screen and (min-width: 768px) {
  .hamburger.is-open span:nth-child(4) {
    width: 6.25rem;
    top: -1.125rem;
  }
}

.hamburger.is-open span:nth-child(5) {
  top: -1.125rem;
  transform: rotate(-135deg);
}
@media screen and (min-width: 768px) {
  .hamburger.is-open span:nth-child(5) {
    top: -1.125rem;
  }
}

