@use "global" as *;

//ルートフォントはhtmlに必ずかく！functionsとsettingにも関連記述あり
html {
  //~375px
  @media (max-width: 375px) {
    font-size: vw(375, 16);
  }

  //375px~787px
  font-size: 16px;
  //font-size: 100%;//ブラウザの設定で特大とかにしていると文字が大きくなったり小さくなる可能性があるので、16pxの固定値の方がよい

  //768px～1099px
  @include mq("md") {
    font-size: vw(strip-unit($inner), 16);
    //例 16 / 100 * 100vw = 1.45455vw
  }

  //inner1100px~max-screen
  @media (min-width: $inner) {
    font-size: 16px;
  }
}

/* ホバー */
a {
  text-decoration: none;
  color: inherit;
  
  @media (any-hover:hover) {
    transition: opacity .3s;
    cursor: pointer;
  }
}

body {
  color: $black;
  font-weight: $regular;
  letter-spacing: .05em;
  background: $white;
  -webkit-font-smoothing: antialiased;//Mac　フォントなめらか
  -moz-osx-font-smoothing: grayscale;//Firefox フォントなめらか
  overflow-wrap: break-word;//単語全体があふれずに行内に配置できない場合にのみ、改行を生成
  //以下でフッターを下部にする
  display: flex; //フレックスボックス
  flex-flow: column; //要素を縦に並べる
  min-height: 100vh; //最小でも画面の高さ分のbodyの高さを担保する
}

main {//フッターを下部に
  flex: 1; //画面の高さから他の同レイヤーの要素の高さを引いた値に
}

h1,h2,h3,h4,h5,h6 {
  font-weight: $regular;
}

img {
  max-width: 100%;//画面からはみ出ないように
  height: auto;    // 縦横比を維持
  vertical-align: bottom;  // 画像下のスペースを消すために、垂直方向の行揃えをbottomに
}




