@use "global" as *;

.loader-bg {
  position: fixed;
  background-color: rgba(255,255,255, .8);
  height: 100%;
  left: 0px;
  top: 0px;
  width: 100%;
  z-index: map-get($layer, 'loader');
}

.loader-bg .ball-pulse-sync {
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  z-index: map-get($layer, 'loader');
}

.ball-pulse-sync > div {
  background-color: $blue;
}