@use "global" as *;

.swipers {//ページネーションを外に出すため
  position: relative;
  @include mq() {
    width: min(100%, rem(400));
  }
}

.swiper.swiper {
  width: min(100%, rem(600));//中途半端な画面幅を考慮

  @include mq() {
    width: min(100%, rem(400));
  }
}

.swiper-wrapper > .swiper-slide {
  width: 100%;
  aspect-ratio: 1500/1000;
  // aspect-ratio: 375/261;

  @include mq() {
  }
}

.swiper-wrapper > .swiper-slide img {
  height: 100%;//ここないとaspect-ratioきかない
  object-fit: cover;
}


.swiper-wrapper {//ページネーションを外に出す
  padding-bottom: rem(30);//16で一番下にきて大きさの8とmarginの8

  @include mq() {
    // padding-bottom: rem(43);//18で一番下にきて大きさの8とmarginの17
    padding-bottom: 0;
  }
}

.swiper-horizontal>.swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  @include mq() {
    bottom: rem(-30);
  }
}

.swiper-pagination {
  
  @include mq() {
    text-align: left;
  }
}

.swiper-pagination-bullet {
  background: $gray-sub;
  opacity: 1;
  width: rem(8);
  height: rem(8);

  &-active {
    background: $blue;
  }
}

//ページネーションの余白
.swiper-horizontal>.swiper-pagination-bullets .swiper-pagination-bullet, .swiper-pagination-horizontal.swiper-pagination-bullets .swiper-pagination-bullet {
  margin:0 rem(6);
}
